* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  color: #333;
  font-size: 20px;
  line-height: 1.5;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}

.scrollPosition {
  background: indianred;
  color: indigo;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100;
}

.header-container {
  z-index: 1;
}

.header {
  width: 100%;
  background: #009FE3;
  padding: 50px 30px;
  font-weight: bold;
}

.sticky-inline {
  width: 100%;
  background: #ff8080;
  padding: 30px;
}

.sticky-inline-odd {
  background: #e6ff60;
}

.sticky-inline-sidebar {
  overflow: hidden;
  height: 1000px;
  /* pattern from https://leaverou.github.io/css3patterns/#japanese-cube */
  background-color:#556;
  background-image: linear-gradient(30deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
  linear-gradient(150deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
  linear-gradient(30deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
  linear-gradient(150deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
  linear-gradient(60deg, #99a 25%, transparent 25.5%, transparent 75%, #99a 75%, #99a),
  linear-gradient(60deg, #99a 25%, transparent 25.5%, transparent 75%, #99a 75%, #99a);
  background-size:80px 140px;
  background-position: 0 0, 0 0, 40px 70px, 40px 70px, 0 0, 40px 70px;
}

.sticky-placeholder {
  padding-bottom: 100px;
}

.placeholder {
  width: 100%;
  height: 2500px;
  border-bottom: 5px solid #333;
  color: #fff;
  /* pattern from http://lea.verou.me/css3patterns/#stars by Nicolas Gallagher */
  background:
    linear-gradient(324deg, #fdfdfd 4%,   transparent 4%) -70px 43px,
    linear-gradient( 36deg, #fdfdfd 4%,   transparent 4%) 30px 43px,
    linear-gradient( 72deg, #e3d7bf 8.5%, transparent 8.5%) 30px 43px,
    linear-gradient(288deg, #e3d7bf 8.5%, transparent 8.5%) -70px 43px,
    linear-gradient(216deg, #e3d7bf 7.5%, transparent 7.5%) -70px 23px,
    linear-gradient(144deg, #e3d7bf 7.5%, transparent 7.5%) 30px 23px,

    linear-gradient(324deg, #fdfdfd 4%,   transparent 4%) -20px 93px,
    linear-gradient( 36deg, #fdfdfd 4%,   transparent 4%) 80px 93px,
    linear-gradient( 72deg, #e3d7bf 8.5%, transparent 8.5%) 80px 93px,
    linear-gradient(288deg, #e3d7bf 8.5%, transparent 8.5%) -20px 93px,
    linear-gradient(216deg, #e3d7bf 7.5%, transparent 7.5%) -20px 73px,
    linear-gradient(144deg, #e3d7bf 7.5%, transparent 7.5%) 80px 73px;
    background-color: #fdfdfd;
    background-size: 100px 100px;
}

.wrapper {
  margin: 0 50px;
}
